import Vue from "vue"
import Router from "vue-router"

Vue.use(Router)

/**
 * Import component for lazy loading
 * @param view
 * @param comp
 * @returns () => import( 'chunk name', 'path to component' )
 */

const ic = (view: string) => {
	return () => import(/* webpackChunkName: "view-[request]" */ `@/views/${view}.vue`)
}
const icc = (comp: string) => {
	return () => import(/* webpackChunkName: "view-[request]" */ `@/components/${comp}.vue`)
}

const router = new Router({
	mode: "history",
	base: process.env.BASE_URL,
	routes: [
		{
			path: "/", component: ic("OnBoard"),
			children: [
				{ path: "", component: ic("TheWelcome") },
				{ path: "signin", component: ic("TheLogin"), name: "signin" },
				{ path: "signup", component: ic("CreateAccount") }
			]
		},
		{ path: "/home", component: ic("Home") },
		{ path: "/card", component: ic("CardAdd") },
		{ path: "/welcome", component: ic("Welcome") },
		{ path: "/cardplan", component: ic("NewCard") },
		{ path: "/plans", component: icc("CardPlans") },
		{path: "/payment", component: icc("UserPayment")}
	]
})

export default router
