







import {Component, Vue} from "vue-property-decorator"

@Component({ components: { } })
export default class CookiePolicy extends Vue {

	private created(): void {
		if (window.localStorage.getItem("cookiesstatus") === "allow")
			this.$emit("hide")
	}

	private allowCookies(): void {
		window.localStorage.setItem("cookiesstatus", "allow")
		this.$emit("hide")
	}
}
