import {Component, Vue} from "vue-property-decorator"
import PortalClient from "@chaosinsight/portalclient"
import "@chaosinsight/yogipeople-portalclient"

@Component({ components: { } })
class Portal extends Vue {
	public isAuthenticated = false
	public isStudent: boolean | null = null

	// tslint:disable-next-line
	private _client!: PortalClient

	public created(): void {
		this._client = new PortalClient(process.env.VUE_APP_PORTAL_PATH as string, "1.14")
	}

	public get client(): PortalClient {
		return this._client
	}

	public login(email: string, password: string): Promise<void> {
		return this.client.call.access.login(email, password)
			.response
			.then(session => {
				this.isStudent = session.UserType === 1
				this.isAuthenticated = true
			})
	}
}

export default new Portal()
